.feedback {
  display: flex;
  flex-direction: column;
  text-align: center;
  position: absolute;
  right: 279px;
  top: -519px;
}

.feedback__title {
  width: 327px;
  font-size: 17px;
  font-weight: 600;
  line-height: 28px;
  color: $white;
  margin-bottom: 0;
}

.feedback__name,
.feedback__nubmer,
.feedback__pull {
  width: 327px;
  height: 48px;
  border-radius: 4px;
  padding-top: 14px;
  padding-bottom: 10px;
  box-sizing: border-box;
  border: 1px solid $light-blue;
  margin-top: 20px;
}

.feedback__name,
.feedback__nubmer {
  background: $white;
  border: 1px solid $gray;
  padding-left: 30px;

  &::placeholder {
    font-size: 15px;
    line-height: 24px;
    color: $black-low;
    opacity: 0.5;
  }
}

.feedback__pull {
  background: $light-blue;
  font-size: 15px;
  line-height: 21px;
  font-weight: 600;
  text-align: center;
  cursor: pointer;
}

@media (max-width: $tablet-width) {
  .feedback {
    position: static;
    margin: 0 auto;
    max-width: 321px;
  }

  .feedback__title {
    color: $black;
    max-width: 321px;
    margin-top: 70px;
  }

  .feedback__name,
  .feedback__nubmer,
  .feedback__pull {
    width: 321px;
  }

  .feedback__name {
    margin-top: 30px;
  }

  .feedback__name,
  .feedback__nubmer {
    padding-left: 21px;
  }

  .feedback__pull {
    height: 51px;
  }
}

@media (max-width: $mobile-width) and (min-width: 320px) {
  .feedback__name,
  .feedback__nubmer,
  .feedback__pull {
    width: 288px;
    align-self: center;
    margin-top: 20px;
  }

  .feedback__title {
    max-width: 288px;
    display: block;
    margin: 0 auto;
    margin-top: 70px;
    text-align: center;
    letter-spacing: -0.005em;
  }
}
